#contact{
    width:100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('../../images/contact.jpg');
    position: inherit;
    z-index: 0;
    color: rgb(26, 25, 25);
    text-shadow: black;
    text-align:center;
    display: flex;
    justify-content: center;
    align-items: center;
}
.row{
    color: rgb(8, 8, 8);
    text-shadow: black;
    text-align:center;
    vertical-align: middle;
}
